
















import { Component, Vue, Prop } from "vue-property-decorator";

@Component({})
export default class Elmmenu extends Vue {
  @Prop() private path!: string;
  formtitle = {
    name: "",
    context: ""
  };
  loading = false;
  async submitForm() {
    this.loading = true
    const res = await this.$http.post(`/${this.path}`, this.formtitle);
    if (res.status === 200) {
      this.$message({
        showClose: true,
        message: "提交成功",
        type: "success"
      });
      this.$router.push({path:'/titlelist',query:{id:this.path}})
    } else {
      this.$message({
        showClose: true,
        message: "提交失败,请重新提交",
        type: "error"
      });
    }
    this.loading = false;
  }
  mounted() {}
}
